








import Vue from 'vue';
import Component from 'vue-class-component';
import { Getter } from '@/decorators';

@Component({
	name: 'ColorBox',
	props: {
		title: String
	}
})
export default class ColorBox extends Vue {
	@Getter('colour') colour: string;

	title: string;

	get hasTitle() {
		return !!this.title;
	}
	get boxStyle() {
		return {
			'background-color': this.colour
		};
	}
}



